'use client'

import { useEffect, useState } from 'react'

/**
 * Hook pour d√©tecter si l'utilisateur pr√©f√®re les animations r√©duites
 * Respecte la pr√©f√©rence syst√®me prefers-reduced-motion
 */
export function useReducedMotion(): boolean {
  const [prefersReducedMotion, setPrefersReducedMotion] = useState(false)

  useEffect(() => {
    // V√©rifier la pr√©f√©rence syst√®me
    const mediaQuery = window.matchMedia('(prefers-reduced-motion: reduce)')
    
    // D√©finir la valeur initiale
    setPrefersReducedMotion(mediaQuery.matches)

    // √âcouter les changements de pr√©f√©rence
    const handleChange = (event: MediaQueryListEvent) => {
      setPrefersReducedMotion(event.matches)
    }

    // Support pour les anciens navigateurs
    if (mediaQuery.addEventListener) {
      mediaQuery.addEventListener('change', handleChange)
    } else {
      // Fallback pour Safari < 14
      mediaQuery.addListener(handleChange)
    }

    return () => {
      if (mediaQuery.removeEventListener) {
        mediaQuery.removeEventListener('change', handleChange)
      } else {
        mediaQuery.removeListener(handleChange)
      }
    }
  }, [])

  return prefersReducedMotion
}



